@charset "UTF-8";

// Color
@import "components/color-variables";
@import "components/color-classes";

// Variables;
@import "components/variables";

@mixin background-opacity($color, $opacity: 0.3) {
  background: $color; /* The Fallback */
  background: rgba($color, $opacity);
}

body {
  background-color: #121212;
}

.container {

}

.card-content {

}

nav, footer.page-footer {
  @include background-opacity(white, 0.15);
  margin: 7px 0;
}

.card {
  @include background-opacity(white, 0.05);
}

div.card-image-with-text {
  display: block;
  position: relative;
}

div.card-image-with-text:after  {
  content:'';
  position:absolute;
  left:0; top:0;
  width:100%; height:100%;
  background-image: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.90));
}

i.green-icon {
  color: darkgreen;
}

i.orange-icon {
  color: darkorange;
}

i.red-icon {
  color: red;
}

#contact input,textarea {
  color: white;
}

div#contact a {
  margin: 3px;
}

a.linkedin-image img{

}

a.github-image img{
  color: white;
}

img.profile {
  margin-top: 5px;
}

.collapsible {
  border: none;
  .collapsible-header {
    @include background-opacity(white, 0.05);
    border-color: grey;
  }
  .collapsible-body {
    @include background-opacity(white, 0.10);
    border: none;
  }
}

ul.collapsible {
    margin-top: 15px;
}

.left-icon {
  margin-right: 7px;
}

div.card-content {
  p {
    text-align: justify;
  }
  li {
    text-align: justify;
  }

}

.justify {
  p {
    text-align: justify;
  }
  text-align: justify;
}

.card-content {
  ul {
    li {
      list-style-type: square;
    }
  }
}

ul.square {
  li {
    list-style-type: square;
  }
}

#slide-out {
  background-color: #353535;
  a {
    color: white;
  }

}

.footer-title {
  font-weight: bold;
}

.logo-margin {
  margin-left: 10px;
  font-weight: bold;
}

h1,h2 {
  font-size: 1.64rem;
  line-height: 110%;
  margin: 1.0933333333rem 0 0.656rem 0;
}

blockquote {
  font-style: italic;
}